import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import { Product, Loading } from "../components";
import { PRODUCT_DETAIL } from "../constants/apiRoutes";
import useSWR from "swr";
import { Context } from "../store/Store";
import * as ROUTES from "../constants/routes";
import axios from "../utils/axios";

export function ProductDetailsContainer() {
  const history = useHistory();
  const [prod] = useState({ ...history.location.state });
  const { data } = useSWR(`${PRODUCT_DETAIL}/${prod.id}`);
  const { state } = useContext(Context);
  const { user, host } = state;
  const [processing, setProcessing] = useState(false);
  const [lifeCycleOption, setlifeCycleOption] = useState([]);
  const [validateSize, setValidateSize] = useState({ min: 0, id: "" });

  if (!data) return <Loading />;

  if (data.size.length && validateSize.min === 0) {
    setValidateSize({ ...validateSize, min: 1 });
  }

  function handleClick(e) {
    if (validateSize.min > 0 && validateSize.id === "") {
      alert("O tamanho no foi selecionado");
      return;
    }

    let hasNotSelectRequiredItems = data.collection.some((c) => {
      if (c.min_quantity > parseInt(c.extra.reduce((sum, extra) => sum + (extra.quantity ? extra.quantity : 0), 0))) {
        alert("Existem opes obrigatorias que no foram selecionadas");
        return true;
      }
    });
    if (hasNotSelectRequiredItems) return;

    if (user) {
      setProcessing(true);

      let productPost = {
        cart_id: "",
        company_id: data.product.company_id,
        note: "Teste",
        product_id: data.product.product_id,
        quantity: 1,
        user_id: user.user_id,
        source: host.company.company_id,
      };

      if (data.collection) {
        productPost.extra = [];
        lifeCycleOption.map((c) => {
          c.extra.map((e) => {
            productPost.extra.push({
              ...e,
              collection_id: c.collection_id,
              user_id: user.user_id,
              cart_extra_id: "",
              cart_id: "",
              product_id: data.product.product_id,
            });
          });
        });
      }

      if (data.size.length) {
        data.size.map((s) => {
          if (s.size_id === validateSize.id) {
            productPost.size_id = s.size_id;
            productPost.size_name = s.name;
          }
        });
      }

      axios
        .post(ROUTES.CART, productPost)
        .then((res) => {
          history.push(ROUTES.CART);
        })
        .catch((error) => {
          // setError(error.message);
          setProcessing(false);
        });
    } else {
      history.push(ROUTES.SIGN_IN);
    }
  }

  function handleClickRadio(e) {
    setValidateSize({ ...validateSize, id: e.currentTarget.value });
  }

  function handleClickOption(collection, extra, type) {
    let hasOne = false;
    let hasOneExtra = false;
    let hasToUpdate = false;
    let maxInCollection = false;
    let updatedList = lifeCycleOption.map((o) => {
      if (o.collection_id === collection.collection_id) {
        hasOne = true;
        switch (type) {
          case "PLUS":
            if (parseInt(o.max) > o.extra.reduce((sum, extras) => sum + extras.quantity, 0)) {
              hasToUpdate = true;
              return {
                ...o,
                extra: o.extra.map((e) => {
                  if (e.extra_id === extra.extra_id) {
                    hasOneExtra = true;
                    return {
                      ...e,
                      quantity: e.quantity + 1 >= o.max ? o.max : e.quantity + 1,
                    };
                  }
                  return e;
                }),
              };
            } else {
              maxInCollection = true;
            }
            break;
          case "MINUS":
            if (o.extra.reduce((sum, extras) => sum + extras.quantity, 0) > 0) {
              hasToUpdate = true;
              return {
                ...o,
                extra: o.extra.map((e) => {
                  if (e.extra_id === extra.extra_id) {
                    return { ...e, quantity: e.quantity - 1 < 0 ? 0 : e.quantity - 1 };
                  }
                  return e;
                }),
              };
            }
            break;
          case "RADIO":
            hasToUpdate = true;
            return { ...o, extra: [{ extra_id: extra.extra_id, quantity: 1, name: extra.name }] };
            break;
        }
      }
      return o;
    });
    if (!hasOne && (type === "PLUS" || type === "RADIO")) {
      setlifeCycleOption([
        ...lifeCycleOption,
        {
          collection_id: collection.collection_id,
          min: collection.min_quantity,
          max: collection.max_quantity,
          extra: [{ extra_id: extra.extra_id, quantity: 1, name: extra.name }],
        },
      ]);
    }
    if (!hasOneExtra && hasOne && type === "PLUS" && !maxInCollection) {
      let updatedList = lifeCycleOption.map((c) => {
        if (c.collection_id === collection.collection_id) {
          return {
            ...c,
            extra: [...c.extra, { extra_id: extra.extra_id, quantity: 1, name: extra.name }],
          };
        }
        return c;
      });
      setlifeCycleOption(updatedList);
    } else {
      if (hasToUpdate) setlifeCycleOption(updatedList);
    }
  }

  lifeCycleOption.map((o) => {
    o.extra.map((equantity) => {
      data.collection.map((c) => {
        c.extra.map((eCart) => {
          if (equantity.extra_id === eCart.extra_id && c.collection_id === o.collection_id) {
            eCart.quantity = equantity.quantity;
          }
        });
      });
    });
  });

  return (
    <Product>
      <Product.Card>
        <Product.Photo src={data.product.photo} />
        <Product.ContentBlock>
          <Product.Group>
            <Product.Title>{data.product.name}</Product.Title>
            <Product.Price>{data.product.price}</Product.Price>
          </Product.Group>
          <Product.Group boxshadow={true}>
            <Product.SubTitle>Descrio</Product.SubTitle>
            <Product.Text>{data.product.description}</Product.Text>
          </Product.Group>

          {Array.isArray(data.size) && data.size.length > 0 && (
            <Product.Group boxshadow={true}>
              <Product.SubTitle>Tamanhos</Product.SubTitle>
              {data.size.map((s) => (
                <Product.SubGroup>
                  <label>
                    <Product.Input
                      onClick={handleClickRadio}
                      type="radio"
                      name="size"
                      id={data.product.product_id}
                      value={s.size_id}
                    />
                    <Product.SubGroup className="subgroup" direction="row">
                      <Product.SubText>{s.name}</Product.SubText>
                      <Product.SubText>{s.price > 0 ? `+ R\$${s.price}` : `R\$${s.price}`}</Product.SubText>
                    </Product.SubGroup>
                  </label>
                </Product.SubGroup>
              ))}
            </Product.Group>
          )}

          {Array.isArray(data.collection) && data.collection.length > 0 && (
            <Product.Group>
              {data.collection.map((c) => (
                <>
                  <Product.SubGroup margin="15px 0 0 0">
                    <Product.Text>{c.name}</Product.Text>
                    <Product.SubText>{c.description}</Product.SubText>
                    <Product.SubText>
                      {c.min_quantity > 0 ? `minimo: ${c.min_quantity} - ` : ""} maximo: {c.max_quantity}
                    </Product.SubText>
                  </Product.SubGroup>
                  {c.extra.map((e) => (
                    <Product.SubGroup direction="row">
                      {c.min_quantity > 0 && c.max_quantity < 2 ? (
                        <label>
                          <Product.Input
                            onClick={() => handleClickOption(c, e, "RADIO")}
                            type="radio"
                            name="extra"
                            id={c.collection_id}
                            value={e.extra_id}
                          />
                          <div>
                            <Product.SubText>{e.name}</Product.SubText>
                            <Product.SubText>{e.price}</Product.SubText>
                          </div>
                        </label>
                      ) : (
                        <>
                          <Product.SubGroup margin="5px 10px 0 0" bordersub="1px solid gray" direction="row">
                            <Product.PlusIcon onClick={() => handleClickOption(c, e, "PLUS")} />
                            <Product.SubText>{e.quantity ? e.quantity : 0}</Product.SubText>
                            <Product.MinusIcon onClick={() => handleClickOption(c, e, "MINUS")} />
                          </Product.SubGroup>
                          <Product.SubGroup>
                            <Product.SubText>{e.name}</Product.SubText>
                            <Product.SubText>{e.description}</Product.SubText>
                            <Product.SubText>{e.price}</Product.SubText>
                          </Product.SubGroup>
                        </>
                      )}
                    </Product.SubGroup>
                  ))}
                </>
              ))}
            </Product.Group>
          )}
          <Product.Button onClick={handleClick} disable={processing ? true : false}>
            {processing ? "Processing..." : "Buy Item"}
          </Product.Button>
        </Product.ContentBlock>
      </Product.Card>
    </Product>
  );
}
