import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Form } from "../components";
import * as ROUTES from "../constants/routes";
import axios from "../utils/axios";

export function SignInContainer() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const history = useHistory();
  const [user, setUser] = useState({ temp_email_id: "" });
  const [error, setError] = useState("");
  const [processing, setProcessing] = useState(false);

  const isInvalid = user.temp_email_id === "";

  const handleSignin = (event) => {
    event.preventDefault();
    setProcessing(true);
    axios
      .post(`/sendemail`, user)
      .then((res) => {
        if (res.data) {
          history.push(ROUTES.CONFIRM_CODE, { user });
        }
      })
      .catch((error) => {
        setError(error.message);
        setProcessing(false);
      });
  };

  return (
    <>
      <Form>
        <Form.Title>Sign In</Form.Title>
        {error && <Form.Error data-testid="error">{error}</Form.Error>}

        <Form.Base onSubmit={handleSignin} method="POST">
          <Form.Input
            type="email"
            placeholder="Email address"
            value={user.temp_email_id}
            onChange={({ target }) =>
              setUser({ ...user, temp_email_id: target.value })
            }
          />
          <Form.Submit
            disabled={isInvalid || processing}
            type="submit"
            data-testid="sign-in"
          >
            {processing ? "Processing..." : "Sign In"}
          </Form.Submit>
        </Form.Base>

        <Form.Text>
          New to Netflix? <Form.Link to="/signup">Sign up now.</Form.Link>
        </Form.Text>
        <Form.TextSmall>
          This page is protected by Google reCAPTCHA to ensure you're not a bot.
          Learn more.
        </Form.TextSmall>
      </Form>
    </>
  );
}
