import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import { Cart, Loading } from "../components";
import { CART } from "../constants/apiRoutes";
import useSWR from "swr";
import { Context } from "../store/Store";
// import "./cart.style.css";

export function CartContainer() {
  const history = useHistory();
  const { state } = useContext(Context);
  const { user, host } = state;

  const { data } = useSWR(`${CART}/${user.user_id},${host.company.company_id},-22.872648599999998,-46.9611568,BR`);

  if (!data) return <Loading />;

  console.log(data);

  const products = data.cart_company.product;
  const cart = data.cart_company;

  if (data.cart_company_empty) {
    return (
      <Cart>
        <Cart.Card style={{ textAlign: "center" }}>Carrinho vazio</Cart.Card>
      </Cart>
    );
  }

  return (
    <Cart>
      {Array.isArray(products) && products.length > 0 && (
        <Cart.Card>
          <Cart.Products>
            <>
              <Cart.SubTitle>Produtos</Cart.SubTitle>
              {products.map((p) => (
                <Cart.Product>
                  <Cart.Quantity minsize="10" fontsize="16">
                    {p.quantity}
                  </Cart.Quantity>
                  <Cart.NameContainer>
                    <Cart.Name>{p.name}</Cart.Name>
                    {p.size_name && <Cart.Size>Tamanho: {p.size_name}</Cart.Size>}
                    {p.extra?.map((e) => (
                      <Cart.SubGroup>
                        <Cart.Quantity minsize="20" fontsize="12">
                          {e.quantity}
                        </Cart.Quantity>
                        <Cart.Extras>{e.name}</Cart.Extras>
                      </Cart.SubGroup>
                    ))}
                  </Cart.NameContainer>
                  <Cart.Price>R${p.total_price}</Cart.Price>
                </Cart.Product>
              ))}
            </>
          </Cart.Products>
          {data.coupon?.length > 0 && <Cart.Coupon>{data.coupon?.length} Cupom disponivel</Cart.Coupon>}
          <Cart.PaymentMethod>Metodo de pagamento</Cart.PaymentMethod>
          <Cart.Total>
            <Cart.SubGroup column={true}>
              <Cart.SubGroup>
                <Cart.Text>SubTotal</Cart.Text>
                <Cart.Price>{cart.subtotal_amount}</Cart.Price>
              </Cart.SubGroup>
              {cart.info.type === "delivery" && (
                <Cart.SubGroup>
                  <Cart.Text>Entrega</Cart.Text>
                  <Cart.Price>{cart.delivery_amount < 1 ? "Grtis" : cart.delivery_amount}</Cart.Price>
                </Cart.SubGroup>
              )}
              {cart.discount_amount && (
                <Cart.SubGroup>
                  <Cart.Text>Desconto</Cart.Text>
                  <Cart.Price>{cart.discount_amount}</Cart.Price>
                </Cart.SubGroup>
              )}

              <Cart.SubGroup>
                <Cart.Text>Total</Cart.Text>
                <Cart.Price>{cart.total_amount}</Cart.Price>
              </Cart.SubGroup>
            </Cart.SubGroup>
          </Cart.Total>
          <Cart.Button>Finalizar Compra</Cart.Button>
        </Cart.Card>
      )}
    </Cart>
  );
}

