import React, { useState, useEffect, useContext } from "react";
import { useHistory } from "react-router-dom";
import { Form } from "../components";
import * as ROUTES from "../constants/routes";
import axios from "../utils/axios";
import { Context } from "../store/Store";

export function ConfirmCodeContainer() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const history = useHistory();
  const { setLocalStorage, state } = useContext(Context);
  const { host } = state;
  const [user, setUser] = useState({
    ...history.location.state.user,
    code_sent: "",
    source: host.company.company_id,
  });
  const [error, setError] = useState("");
  const [processing, setProcessing] = useState(false);

  const isInvalid = user.code_sent === "";

  const handleSignin = (event) => {
    event.preventDefault();
    setProcessing(true);
    axios
      .post(`/verifycodeemail`, user)
      .then((res) => {
        if (res.data instanceof Object) {
          setLocalStorage(res.data);
        } else if (res.data === 1) {
          history.push(ROUTES.REGISTER, {
            user: { email: user.temp_email_id },
          });
        } else {
          setError("Erro ao confirmar o codigo");
          setProcessing(false);
        }
      })
      .catch((error) => {
        setError(error.message);
        setProcessing(false);
      });
  };

  return (
    <>
      <Form>
        <Form.Title>Confirm Code</Form.Title>
        {error && <Form.Error data-testid="error">{error}</Form.Error>}
        <Form.Text>Was sent to you a verification code, put it here to confirm</Form.Text>
        <Form.Base onSubmit={handleSignin} method="POST">
          <Form.Input
            placeholder="Confirm Code"
            value={user.code_sent}
            onChange={({ target }) => setUser({ ...user, code_sent: target.value })}
          />
          <Form.Submit disabled={isInvalid || processing} type="submit" data-testid="confirm-code">
            {processing ? "Processing..." : "Confirm"}
          </Form.Submit>
        </Form.Base>

        <Form.Text onClick={() => history.goBack}>Request a new code</Form.Text>
        <Form.TextSmall>
          This page is protected by Google reCAPTCHA to ensure you're not a bot. Learn more.
        </Form.TextSmall>
      </Form>
    </>
  );
}
