import React, { useContext } from "react";
import { Header } from "../components";
import * as ROUTES from "../constants/routes";
import { Context } from "../store/Store";
import { useHistory } from "react-router-dom";

export function HeaderContainer({ isInLoginProcess, children }) {
  const { state, removeLocalStorage } = useContext(Context);
  const { user, host } = state;
  const history = useHistory();

  function handleClickCart() {
    history.push(ROUTES.CART);
  }

  function handleClickOrders() {
    history.push(ROUTES.ORDERS);
  }

  return (
    <Header src={host.company.banner_photo ? host.company.banner_photo : ""}>
      <Header.Frame>
        <Header.Group>
          <Header.Logo to={ROUTES.HOME} src={host.company.photo} alt="Netflix" />
        </Header.Group>
        <Header.Group>
          {user ? (
            <>
              <Header.OrderIcon size="24" onClick={handleClickOrders} />
              <Header.CartIcon size="24" onClick={handleClickCart} />

              <Header.Profile>
                <Header.Picture src={user.photo} />
                <Header.Dropdown>
                  <Header.Group>
                    <Header.Picture src={user.photo} />
                    <Header.TextLink>{user.full_name}</Header.TextLink>
                  </Header.Group>
                  <Header.Group>
                    <Header.TextLink>Minha conta</Header.TextLink>
                  </Header.Group>
                  <Header.Group>
                    <Header.TextLink
                      onClick={() => {
                        removeLocalStorage();
                      }}
                    >
                      Sign out
                    </Header.TextLink>
                  </Header.Group>
                </Header.Dropdown>
              </Header.Profile>
            </>
          ) : !isInLoginProcess ? (
            <Header.ButtonLink to={ROUTES.SIGN_IN}>Sign In</Header.ButtonLink>
          ) : (
            ""
          )}
        </Header.Group>
      </Header.Frame>
      {children}
    </Header>
  );
}
